
/* fonts */
@font-face {
  font-family: 'Quicksand';
  src: url('fonts/Quicksand-Light.woff2') format('woff2'),
       url('fonts/Quicksand-Light.woff') format('woff');
  font-weight: 300;
  font-style: normal;
}

/* reset & common styles */
body, ul, h1, h2, h3, p, button, a, div{
  margin: 0;
  padding: 0;
  color: white;
  font-family: 'Quicksand', serif;
  letter-spacing: 2px;
  list-style-type: none;
  text-decoration: none;
  position: relative;
}
body{
  background: rgb(54, 157, 216);
  /*background: rgb(89, 200, 211);*/
  /*background: radial-gradient(circle, rgb(54, 157, 216) 0%, rgb(65, 218, 232) 100%);*/
  overflow: hidden;
  -webkit-user-drag: none;
  user-select: none;
}
button{
  color: white;
  padding: 10px 30px;
  font-size: 1em;
  background: transparent;
  border-radius: 50px;
  border: 1px solid white;
  margin: 40px auto 0;
  cursor: pointer;
  opacity: 0.7;
}
.container{
  max-width: 300px;
  margin: 100px auto 40px;
}
.container h3{
  padding-bottom: 10px;
  margin-bottom: 10px;
  border-bottom: 1px solid rgba(255,255,255,0.2);
}
.container li{
  padding: 10px;
  cursor: pointer;
}

/* header */
header{
  display: flex;
  padding: 40px;
  align-items: center;
}
header .title{
  flex-grow: 1;
  margin-left: 20px;
  font-size: 0.6em;
}
header h1{
  border-bottom: 1px solid rgba(255,255,255,0.2);
  padding-bottom: 10px;
}
header .pizza-svg {
  width: 500px;
  overflow: visible;
  stroke: #fff;
  stroke-width: 4;
  stroke-linejoin: round;
  stroke-linecap: round;
}
#chef {
	width: 80px;
  height: auto;
  fill: white;
  overflow: visible;
  stroke: #fff;
  stroke-width: 1;
  stroke-linejoin: round;
  stroke-linecap: round;
}
.logo {
  position: relative;
  flex-grow: 0;
}


/* lists */
li span.active{
  font-weight: bold;
}
li span.active::before{
  content: '>';
  position: relative;
  top: -2px;
  margin-right: 6px;
  transform: scale(0.8, 1);
  display: inline-block;
}

/* pages */
.home h2{
  font-size: 2em;
  margin-bottom: 30px;
}
.home.container,
.order.container{
  text-align: center;
  max-width: 800px;
}
.home button{
  color: white;
  padding: 15px 30px;
  font-size: 1.6em;
  border: 3px solid white;
  margin: 30px 20px;
}
.order p{
  margin: 20px auto;
}
.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.5);
  z-index: 1;
  display: flex;
  align-items: center;
	justify-content: center;
}
.modal {
  max-width: 400px;
  padding: 40px 20px;
  background: white;
  border-radius: 10px;
  text-align: center;
}
.modal button {
  color: #444;
  border-color: #444;
  font-weight: bold;
  margin-top: 20px;
}
.modal p {
  color: #444;
  font-weight: bold;
}
.loader {
  width: 10px;
  height: 10px;
  margin: 40px auto;
  border-radius: 50%;
  background: #fff;
}
#pizza-svg {
  display: block;
  position: absolute;
	/*left: 50%;*/
  /*transform: translate(-50%);*/
  fill: white;
  height: 100px;
  width: 100px;
  opacity: 0.7;
}
#pizza-svg:hover {
  fill: red;
}
